"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Loader2, Wand2 } from "lucide-react";
import { suggestEbooks } from "@/ai/flows/suggest-ebooks-based-on-purchase-history";

export default function AiSuggestions() {
  const [suggestions, setSuggestions] = useState<string>("");
  const [isLoading, setIsLoading] = useState(false);
  const [purchaseHistory, setPurchaseHistory] = useState<string | null>(null);

  useEffect(() => {
    if (typeof window !== 'undefined') {
      const history = localStorage.getItem('purchaseHistory');
      setPurchaseHistory(history);
    }
  }, []);


  const handleGetSuggestions = async () => {
    setIsLoading(true);
    setSuggestions("");
    try {
      const history = purchaseHistory || "User has not purchased any books yet.";
      const result = await suggestEbooks({ purchaseHistory: history });
      setSuggestions(result.suggestedEbooks);
    } catch (error) {
      console.error("Failed to get suggestions:", error);
      setSuggestions("Sorry, I couldn't get any suggestions right now. Please try again later.");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <section className="py-12 md:py-20 bg-card">
      <div className="container mx-auto px-4 text-center">
        <h2 className="font-headline text-3xl md:text-4xl font-bold mb-4">
          Need a Recommendation?
        </h2>
        <p className="max-w-2xl mx-auto text-muted-foreground mb-8">
          Let our AI analyze your taste and suggest your next great read. Based on your purchase history, we'll find books you'll love.
        </p>
        <Button onClick={handleGetSuggestions} disabled={isLoading} size="lg">
          {isLoading ? (
            <Loader2 className="mr-2 h-4 w-4 animate-spin" />
          ) : (
            <Wand2 className="mr-2 h-4 w-4" />
          )}
          Get AI Suggestions
        </Button>

        {suggestions && (
          <Card className="mt-8 max-w-2xl mx-auto text-left shadow-lg">
            <CardHeader>
              <CardTitle className="font-headline flex items-center gap-2">
                <Wand2 className="text-primary" />
                Here are some books you might enjoy:
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-foreground whitespace-pre-line">{suggestions}</p>
            </CardContent>
          </Card>
        )}
      </div>
    </section>
  );
}
