
"use client";

import Image from "next/image";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { getSupabase, type Ebook } from "@/lib/supabase";
import { useCart } from "@/context/cart-context";
import { ShoppingCart, Share2 } from "lucide-react";
import { useEffect, useState } from "react";
import { Skeleton } from "./ui/skeleton";
import { useToast } from "@/hooks/use-toast";

interface EbookCardProps {
  ebook: Ebook;
}

export function EbookCard({ ebook }: EbookCardProps) {
  const { addToCart } = useCart();
  const { toast } = useToast();
  const [imageUrl, setImageUrl] = useState<string>('');
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchImageUrl = async () => {
      setLoading(true);
      if (ebook.coverImageId) {
        const supabase = getSupabase();
        const { data } = supabase.storage.from('ebook-covers').getPublicUrl(ebook.coverImageId);
        setImageUrl(data.publicUrl);
      }
      setLoading(false);
    }
    fetchImageUrl();
  }, [ebook.coverImageId]);

  const handleShare = async () => {
    const shareData = {
      title: ebook.title,
      text: ebook.description,
      url: `${window.location.origin}/ebook/${ebook.id}`
    };
    if (navigator.share) {
      try {
        await navigator.share(shareData);
      } catch (err) {
        console.error("Error sharing:", err);
      }
    } else {
        navigator.clipboard.writeText(shareData.url);
        toast({
            title: "Link Copied",
            description: "Product link copied to clipboard.",
        })
    }
  };


  if (loading) {
    return (
        <div className="space-y-2">
            <Skeleton className="aspect-[2/3] w-full" />
            <Skeleton className="h-5 w-5/6" />
            <Skeleton className="h-4 w-3/4" />
            <Skeleton className="h-8 w-1/2" />
        </div>
    )
  }


  return (
    <Card className="flex flex-col overflow-hidden transition-all hover:shadow-lg hover:-translate-y-1">
      <Link href={`/ebook/${ebook.id}`} className="flex flex-col flex-grow group">
        <CardHeader className="p-0">
          <div className="relative aspect-[2/3] w-full">
            {imageUrl ? (
              <Image
                src={imageUrl}
                alt={`Cover of ${ebook.title}`}
                fill
                className="object-cover"
                sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
              />
            ) : (
                <div className="w-full h-full bg-muted flex items-center justify-center">
                    <span className="text-xs text-muted-foreground">No image</span>
                </div>
            )}
          </div>
        </CardHeader>
        <CardContent className="flex-grow p-4">
          <CardTitle className="mb-1 text-lg font-headline leading-tight group-hover:underline">
            {ebook.title}
          </CardTitle>
          <p className="text-sm text-muted-foreground">{ebook.author}</p>
        </CardContent>
      </Link>
      <CardFooter className="flex flex-wrap items-center justify-between gap-2 p-4 pt-0">
        <p className="text-lg font-bold text-primary">
          GH₵{ebook.price.toFixed(2)}
        </p>
        <div className="flex items-center">
             <Button
                variant="ghost"
                size="icon"
                onClick={handleShare}
                className="shrink-0"
                aria-label="Share this ebook"
            >
                <Share2 className="h-4 w-4" />
            </Button>
            <Button
                onClick={(e) => {
                    e.stopPropagation();
                    addToCart(ebook);
                }}
                size="sm"
                className="shrink-0"
            >
                <ShoppingCart className="h-4 w-4 mr-1" />
                Add
            </Button>
        </div>
      </CardFooter>
    </Card>
  );
}
