'use server';

/**
 * @fileOverview This file defines a Genkit flow for suggesting eBooks to a user based on their purchase history.
 *
 * - suggestEbooks - A function that suggests eBooks based on user's purchase history.
 * - SuggestEbooksInput - The input type for the suggestEbooks function, which includes the user's purchase history.
 * - SuggestEbooksOutput - The output type for the suggestEbooks function, which includes a list of suggested eBooks.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const SuggestEbooksInputSchema = z.object({
  purchaseHistory: z
    .string()
    .describe("The user's purchase history as a string."),
});
export type SuggestEbooksInput = z.infer<typeof SuggestEbooksInputSchema>;

const SuggestEbooksOutputSchema = z.object({
  suggestedEbooks: z
    .string()
    .describe('A list of suggested eBooks based on the purchase history.'),
});
export type SuggestEbooksOutput = z.infer<typeof SuggestEbooksOutputSchema>;

export async function suggestEbooks(input: SuggestEbooksInput): Promise<SuggestEbooksOutput> {
  return suggestEbooksFlow(input);
}

const prompt = ai.definePrompt({
  name: 'suggestEbooksPrompt',
  input: {schema: SuggestEbooksInputSchema},
  output: {schema: SuggestEbooksOutputSchema},
  prompt: `Based on the following purchase history: {{{purchaseHistory}}}, suggest some eBooks the user might enjoy. Return the list of books as a string.
`,
});

const suggestEbooksFlow = ai.defineFlow(
  {
    name: 'suggestEbooksFlow',
    inputSchema: SuggestEbooksInputSchema,
    outputSchema: SuggestEbooksOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
