
import { createClient } from '@supabase/supabase-js'

export type Ebook = {
  id: number;
  title: string;
  author: string;
  description: string;
  price: number;
  coverImageId: string;
  ebook_file_path: string;
  created_at: string;
}

export type ContactMessage = {
  id: number;
  name: string;
  email: string;
  service: string;
  message: string;
  date: string;
  created_at: string;
}


export type Database = {
  public: {
    Tables: {
      ebooks: {
        Row: Ebook,
        Insert: Omit<Ebook, 'id' | 'created_at'>,
        Update: Partial<Ebook>,
      },
      messages: {
        Row: ContactMessage,
        Insert: Omit<ContactMessage, 'id' | 'created_at'>,
        Update: Partial<ContactMessage>,
      }
    }
  }
}

// Ensure you have NEXT_PUBLIC_SUPABASE_URL and NEXT_PUBLIC_SUPABASE_ANON_KEY in your .env.local file
const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY

let supabase: ReturnType<typeof createClient<Database>>;

export function getSupabase() {
    if (!supabase) {
        if (!supabaseUrl || !supabaseAnonKey) {
            // On the server, this might happen during build time.
            // On the client, this would be a configuration error.
            if (typeof window === 'undefined') {
                 console.error("Supabase credentials are not set on the server. Please check your environment variables.");
                 // Return a dummy client to prevent build from crashing
                 return createClient<Database>("https://placeholder.supabase.co", "placeholder-key");
            }
            throw new Error("Supabase credentials are not set. Please check your .env file and ensure they are prefixed with NEXT_PUBLIC_.");
        }
        supabase = createClient<Database>(supabaseUrl, supabaseAnonKey);
    }
    return supabase;
}
